import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import { nanoid } from "nanoid";
import { useHistory } from "react-router-dom";
import {
  Box,
  Button,
  Divider,
  Grid,
  TextField,
  Typography,
} from "@material-ui/core";
import ForumIcon from "@material-ui/icons/Forum";
import { request } from "./socket";
import { makeStyles } from "@material-ui/core/styles";
import VideocamOffIcon from "@material-ui/icons/VideocamOff";
import StreamContext from "./StreamContext";
import getStream from "./getStream";

const useStyles = makeStyles((theme) => ({
  title: {
    marginBottom: theme.spacing(2),
  },
  actions: {
    marginTop: theme.spacing(2),
  },
  create: {
    marginRight: theme.spacing(2),
  },
  join: {
    marginLeft: theme.spacing(2),
  },
  video: {
    width: "640px",
    height: "480px",
    backgroundColor: "rgb(0, 0, 0)",
  },
  player: {
    width: "100%",
    height: "100%",
  },
}));

function Home() {
  const styles = useStyles();
  const history = useHistory();

  const [room, setRoom] = useState("");
  const [showJoinButton, setShowJoinButton] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  const video = useRef<HTMLVideoElement | null>(null);
  const { setStream } = useContext(StreamContext);

  useEffect(() => {
    getStream()
      .then((stream) => {
        if (video.current) {
          video.current.srcObject = stream;
        }
        setStream(stream);
      })
      .catch(console.error);
  }, []);

  useEffect(() => {
    setError(false);

    if (!room.length) setShowJoinButton(false);
    else setShowJoinButton(true);
  }, [room]);

  const join = useCallback(async () => {
    const status = await request({ event: "join", room });

    if (status) {
      history.push(`/${room}`);
    } else {
      setError(true);
    }
  }, [history, room]);

  const create = useCallback(async () => {
    const room = nanoid();
    const status = await request({ event: "create", room });

    if (status) {
      history.push(`/${room}`);
    }
  }, [history]);

  return (
    <Grid container>
      <Grid container item sm={6} alignItems="center" justify="flex-end">
        <Grid container item sm={10} direction="column">
          <Grid container direction="column" className={styles.title}>
            <Typography variant="h5">
              The most secure video conference powered by WebRTC
            </Typography>
            <Typography variant="subtitle1">
              Create new room or join the existing one
            </Typography>
          </Grid>
          <Divider />
          <Grid container item className={styles.actions}>
            <Button
              disabled={loading}
              variant="contained"
              className={styles.create}
              startIcon={<ForumIcon />}
              onClick={create}
            >
              New room
            </Button>
            <TextField
              error={error}
              disabled={loading}
              variant="outlined"
              label="Enter room id"
              value={room}
              onChange={(e) => setRoom(e.target.value)}
            />
            {showJoinButton && (
              <Button disabled={loading} className={styles.join} onClick={join}>
                Join
              </Button>
            )}
          </Grid>
        </Grid>
      </Grid>
      <Grid container item sm={6} justify="center" alignItems="center">
        <Box
          className={styles.video}
          color="text.primary"
          m={1}
          display="flex"
          justifyContent="center"
          alignItems="center"
        >
          {loading && <VideocamOffIcon fontSize="large" />}
          <video
            ref={video}
            onLoadedData={() => setLoading(false)}
            className={styles.player}
            style={{ display: loading ? "none" : "block" }}
            muted
            autoPlay
          />
        </Box>
      </Grid>
    </Grid>
  );
}

export default Home;
