export default function getStream(): Promise<MediaStream | null> {
  if (navigator.mediaDevices) {
    return navigator.mediaDevices
      .getUserMedia({
        video: { width: 640, height: 480, frameRate: 30 },
        audio: true,
      })
      .then(
        (stream) => {
          return stream;
        },
        (error) => {
          switch (error.name) {
            case "NotFoundError":
              alert(
                "Unable to open your call because no camera and/or microphone were found."
              );
              break;
            case "SecurityError":
            case "PermissionDeniedError":
              alert(
                "You can not use video conference w/o granting access to the camera and/or microphone."
              );
              break;
            default:
              alert(
                "Error opening your camera and/or microphone: " + error.message
              );
              break;
          }
          throw error;
        }
      );
  }

  return Promise.resolve(null);
}
