import { useCallback, useEffect, useRef } from "react";
import socket, { request } from "./socket";

export default function useWebRTC(options: {
  stream: MediaStream | null;
  handleTrackEvent: (event: RTCTrackEvent) => void;
  handleICEConnectionStateChangeEvent: (state: RTCIceConnectionState) => void;
  handleSignalingStateChangeEvent: (state: RTCSignalingState) => void;
}) {
  const pc = useRef<RTCPeerConnection | null>(null);

  const close = useCallback(() => {
    if (!pc.current) return;

    pc.current.ontrack = null;
    // @ts-ignore
    pc.current.onremovetrack = null;
    pc.current.onicecandidate = null;
    pc.current.oniceconnectionstatechange = null;
    pc.current.onsignalingstatechange = null;
    pc.current.onicegatheringstatechange = null;
    pc.current.onnegotiationneeded = null;

    pc.current.close();
    pc.current = null;
  }, []);

  const handleTrackEvent = useCallback((event) => {
    options.handleTrackEvent(event);
  }, []);

  const handleNegotiationNeededEvent = useCallback(async () => {
    if (!pc.current) return;

    const offer = await pc.current.createOffer();
    await pc.current.setLocalDescription(offer);
    await request({ event: "call", offer: pc.current.localDescription });
  }, []);

  const handleICECandidateEvent = useCallback(async (event) => {
    if (!event.candidate) return;
    await request({ event: "candidate", candidate: event.candidate });
  }, []);

  const handleICEConnectionStateChangeEvent = useCallback(() => {
    if (!pc.current) return;
    const state = pc.current?.iceConnectionState;

    switch (state) {
      case "closed":
      case "failed":
      case "disconnected":
        close();
    }

    options.handleICEConnectionStateChangeEvent(state);
  }, []);

  const handleSignalingStateChangeEvent = useCallback(() => {
    if (!pc.current) return;
    const state = pc.current.signalingState;

    switch (state) {
      case "closed":
        close();
    }

    options.handleSignalingStateChangeEvent(state);
  }, []);

  const createPeerConnection = useCallback(() => {
    const c = new RTCPeerConnection({
      iceServers: [
        {
          urls: "stun:stun.l.google.com:19302",
        },
      ],
    });

    c.onnegotiationneeded = handleNegotiationNeededEvent;
    c.ontrack = handleTrackEvent;
    c.onicecandidate = handleICECandidateEvent;
    c.oniceconnectionstatechange = handleICEConnectionStateChangeEvent;
    c.onsignalingstatechange = handleSignalingStateChangeEvent;

    // @ts-ignore
    c.onremovetrack = (event) => console.log("onremovetrack", event);
    c.onicegatheringstatechange = () =>
      console.log("onicegatheringstatechange");

    return c;
  }, []);

  useEffect(() => {
    if (!options.stream) return;

    async function call(msg: { offer: RTCSessionDescriptionInit }) {
      console.log("call", msg);

      pc.current = createPeerConnection();

      await pc.current.setRemoteDescription(
        new RTCSessionDescription(msg.offer)
      );

      // @ts-ignore
      options.stream
        .getTracks()
        // @ts-ignore
        .forEach((track) => pc.current.addTrack(track, options.stream));

      const answer = await pc.current.createAnswer();
      await pc.current.setLocalDescription(new RTCSessionDescription(answer));
      await request({ event: "answer", answer: pc.current.localDescription });
    }

    async function answer(msg: { answer: RTCSessionDescriptionInit }) {
      console.log("answer", msg);

      if (!pc.current) return;

      await pc.current.setRemoteDescription(
        new RTCSessionDescription(msg.answer)
      );
    }

    async function candidate(msg: { candidate: RTCIceCandidate }) {
      console.log("candidate", msg);

      if (!pc.current) return;

      await pc.current.addIceCandidate(msg.candidate);
    }

    socket.on("call", call);
    socket.on("answer", answer);
    socket.on("candidate", candidate);

    return () => {
      socket.off("call", call);
      socket.off("answer", answer);
      socket.off("candidate", candidate);
    };
  }, [options.stream]);

  return {
    call() {
      pc.current = createPeerConnection();

      // @ts-ignore
      options.stream
        .getTracks()
        // @ts-ignore
        .forEach((track) => pc.current.addTrack(track, options.stream));
    },
    close,
  };
}
