import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import { useHistory, useParams } from "react-router-dom";

import { Box, Grid } from "@material-ui/core";
import { request } from "./socket";
import { makeStyles } from "@material-ui/core/styles";
import useWebRTC from "./useWebRTC";
import StreamContext from "./StreamContext";
import VideocamOffIcon from "@material-ui/icons/VideocamOff";
import getStream from "./getStream";

const useStyles = makeStyles({
  remote: {
    position: "relative",
    width: "640px",
    height: "480px",
    backgroundColor: "rgb(0, 0, 0)",
  },
  local: {
    position: "absolute",
    top: "-120px",
    right: "-160px",
    width: "320px",
    height: "240px",
    backgroundColor: "rgb(0, 0, 0)",
    border: "1px solid white",
  },
  video: {
    width: "100%",
    height: "100%",
  },
});

interface RoomParams {
  id: string;
}

function Room() {
  const styles = useStyles();
  const params: RoomParams = useParams();
  const history = useHistory();

  const [joined, setJoined] = useState(false);
  const [loading, setLoading] = useState(true);
  const [connecting, setConnecting] = useState(true);

  const { stream, setStream } = useContext(StreamContext);

  const local = useRef<HTMLVideoElement | null>(null);
  const remote = useRef<HTMLVideoElement | null>(null);

  const disconnect = useCallback(() => {
    if (remote.current) {
      remote.current.removeAttribute("srcObject");
    }
  }, []);

  useEffect(() => {
    request({ event: "join", room: params.id }).then((status) => {
      if (!status) {
        history.push("/");
      } else {
        setJoined(true);
      }
    });
  }, []);

  useEffect(() => {
    if (!joined) return;

    if (stream && local.current) {
      local.current.srcObject = stream;
    } else {
      getStream()
        .then((stream) => {
          if (!stream) return;
          if (local.current) {
            local.current.srcObject = stream;
          }
          setStream(stream);
        })
        .catch(console.error);
    }
  }, [joined]);

  const webrtc = useWebRTC({
    stream: stream,
    handleTrackEvent: (event) => {
      if (remote.current) {
        remote.current.srcObject = event.streams[0];
      }
    },
    handleICEConnectionStateChangeEvent: (state) => {
      switch (state) {
        case "closed":
        case "failed":
        case "disconnected":
          disconnect();
          break;
        default:
          console.log(state);
          break;
      }
    },
    handleSignalingStateChangeEvent: (state) => {
      switch (state) {
        case "closed":
          disconnect();
          break;
        default:
          console.log(state);
          break;
      }
    },
  });

  useEffect(() => {
    if (loading) return;

    request({
      event: "participants",
      room: params.id,
    })
      .then((participants) => {
        console.log("participants", participants);

        // we are alone here, wait for someone to call us
        if (!participants.length) return;

        webrtc.call();
      })
      .catch(console.error);
  }, [loading]);

  return (
    <Grid container alignItems="center" justify="center">
      <Grid container item sm={10} direction="column" justify="center">
        <Grid container justify="center">
          <Box
            className={styles.remote}
            color="text.primary"
            m={1}
            display="flex"
            justifyContent="center"
            alignItems="center"
          >
            {connecting && <VideocamOffIcon fontSize="large" />}
            <video
              ref={remote}
              onLoadedData={() => setConnecting(false)}
              className={styles.video}
              style={{ display: connecting ? "none" : "block" }}
              autoPlay
            />
            <Box
              className={styles.local}
              color="text.primary"
              m={1}
              display="flex"
              justifyContent="center"
              alignItems="center"
            >
              {loading && <VideocamOffIcon fontSize="large" />}
              <video
                ref={local}
                onLoadedData={() => setLoading(false)}
                className={styles.video}
                style={{ display: loading ? "none" : "block" }}
                muted
                autoPlay
              />
            </Box>
          </Box>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Room;
